import { Component, OnInit } from '@angular/core';
import { ToastController } from '@ionic/angular';
import { RestApiService } from 'src/app/services/rest-api.service';
import { AuthenticateService } from 'src/app/services/authentication.service';

@Component({
  selector: 'app-verifiedplans',
  templateUrl: './verifiedplans.page.html',
  styleUrls: ['./verifiedplans.page.scss'],
})
export class VerifiedplansPage implements OnInit {

  objs: Array<any>;
  constructor(public toastController: ToastController,public restApi: RestApiService, public authService: AuthenticateService) { }

  ngOnInit() {
    this.getAll();
  }

  getAll() {
    this.restApi.post('accessor/get-verified-plans', {}).subscribe((res: any) => {
      if (res && res.status) {
        if (res.status == 'success') {
          this.objs = res.data;
          console.log(this.objs);
        } else {
          this.toastController.create({
            message: res.message,
            duration: 2000
          }).then(toast => toast.present());
        }
      }
    }, error => {
      this.toastController.create({
        message: 'Something went wrong.',
        duration: 2000
      }).then(toast => toast.present());
    });
  }
}
